import React from "react";

import { Panel, PanelHeader, HeaderButton, osname, IOS, Link, Select, Checkbox, Button,
         FormLayout, Input, Div} from '@vkontakte/vkui';

import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';

class Data2Panel extends React.Component {
    render() {
        return (
            <Panel id={ this.props.id } theme="white">
                <PanelHeader
                    left={
                        <HeaderButton
                            onClick={ this.props.back.bind(this) }
                        >
                            { osname === IOS ? <Icon28ChevronBack /> : <Icon24Back /> }
                        </HeaderButton>
                    }
                    theme="light"
                >
                     
                </PanelHeader>
                <FormLayout>
                    <Select top=" " placeholder="  ">
                        <option value="1">(O(I)RH+)    </option>
                        <option value="2">(O(I)RH-)    </option>
                        <option value="3">(A(II)RH+)    </option>
                        <option value="4">(A(II)RH-)    </option>
                        <option value="5">(B(III)RH+)    </option>
                        <option value="6">(B(III)RH-)    </option>
                        <option value="7">(AB(III)RH+)    </option>
                        <option value="8">(AB(III)RH-)    </option>
                        <option value="9">()  </option>
                    </Select>
                    <Select top="Kell-" placeholder=" Kell-">
                        <option value="1"> </option>
                        <option value="2"></option>
                        <option value="3" ></option>
                    </Select>
                    <Checkbox top="    " item="one"> </Checkbox>
                    <Checkbox item="two"></Checkbox>
                    <Checkbox item="three"></Checkbox>
                    <Checkbox item="four"></Checkbox>
                    <Checkbox item="five"></Checkbox>
                    <Checkbox item="six"></Checkbox>
                    <Div style={{ textAlign: "center" }}>
                           .    
                        <Link href="https://donorsearch.org/requirements"> </Link>.
                               ,  ,    .
                            .
                    </Div>
                    <Input top=" " type="text" placeholder="xx.xx.xxxx" />
                    <Checkbox item="seven">     ,   </Checkbox>
                    <Div style={{ textAlign: "center" }}>
                          .     
                           ,      .
                        <Link href="https://donorsearch.org/donorstvo-kostnogo-mozga">   </Link>
                    </Div>
                    <Checkbox item="eight">   </Checkbox>
                    <Button
                        onClick={ this.props.next.bind(this, "carddata3-panel") }
                        size="xl"
                    ></Button>
                </FormLayout>
            </Panel>
        );
    }
}

export default Data2Panel;



